@import url(https://fonts.googleapis.com/css2?family=Zen+Kaku+Gothic+Antique:wght@400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--main-color: $primary}.App{height:100vh;display:flex;flex-direction:column;font-family:"Zen Kaku Gothic Antique", sans-serif;background-color:#f1f3f8}@media (prefers-color-scheme: dark){.App{background:linear-gradient(0deg, #1d1e24 0%, #282a32 100%)}}.header{height:70px;display:flex;justify-content:space-between;align-items:center;background-color:#5a3dfd;border-bottom:1px solid rgba(0,0,0,0.1);font-size:1.3em;color:#fff;font-weight:500;padding:0 2rem;box-shadow:10px 10px 10px rgba(0,0,0,0.1);background:linear-gradient(0deg, #5a3dfd 0%, #3c1afd 100%)}@media (prefers-color-scheme: dark){.header{background:transparent;box-shadow:none;border:none;color:white}}.header a{color:#fff;font-size:0.8em;text-decoration:none;line-height:0}.header a:hover{opacity:0.7}.header__title{display:flex;align-items:center;justify-content:center;grid-gap:0.5rem;gap:0.5rem}.header__title h1{font-weight:700;font-size:1em}.header__links-group{display:flex;align-items:center;grid-gap:2rem;gap:2rem}.app-empty{display:flex;flex-direction:column;justify-content:center;align-items:center;flex:1 1}@media (prefers-color-scheme: dark){.app-empty{color:white}}.app-empty__icon{height:100px;width:100px;opacity:0.2}.app-empty p{opacity:0.4;width:300px;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.empty{background-color:#5a3dfd}.grid{display:grid;grid-template-columns:1fr 1fr;grid-template-columns:1fr 1fr;height:200px;width:100%}@media (prefers-color-scheme: dark){.grid__cell{border-color:rgba(255,255,255,0.1) !important}}.grid__cell:nth-child(1){border:1px solid rgba(0,0,0,0.3)}.grid__cell:nth-child(2){border-top:1px solid rgba(0,0,0,0.3);border-right:1px solid rgba(0,0,0,0.3);border-bottom:1px solid rgba(0,0,0,0.3)}.grid__cell:nth-child(3){border-left:1px solid rgba(0,0,0,0.3);border-right:1px solid rgba(0,0,0,0.3);border-bottom:1px solid rgba(0,0,0,0.3)}.grid__cell:nth-child(4){border-right:1px solid rgba(0,0,0,0.3);border-bottom:1px solid rgba(0,0,0,0.3)}.grid__cell:hover{background-color:rgba(0,0,0,0.1)}.grid__cell select{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;height:100%;text-align:center;border:none;background-color:transparent;font-size:1em;outline:none;cursor:pointer}@media (prefers-color-scheme: dark){.grid__cell select{color:white}}.grid__cell select:last-child{font-family:"Noto Sans";font-size:2em;font-weight:700}.grid__cell select option{padding:2rem 0}.exos{display:flex;flex-flow:row;justify-content:center;grid-gap:2rem;gap:2rem;flex:1 1;margin:1rem 0;overflow:auto}.exo{position:relative;width:250px;display:flex;flex-direction:column;justify-content:space-between;background-color:#fff;border:1px solid rgba(0,0,0,0.2);box-shadow:10px 10px 20px rgba(0,0,0,0.05)}@media (prefers-color-scheme: dark){.exo{background:#343640;border:1px solid rgba(255,255,255,0.1);color:white}}.exo__body{padding-top:1.5rem;padding-left:1.5rem;padding-right:1.5rem}.exo__header{display:grid;grid-template-rows:auto}.exo__time{font-weight:700}.exo__sections{margin-top:1rem;height:100%;border-top:1px solid rgba(0,0,0,0.2);box-shadow:inset 0px 5px 10px rgba(0,0,0,0.1)}.exo__section{position:relative;display:grid;cursor:pointer;padding:1rem 1.5rem;box-sizing:border-box}.exo__section:hover{background-color:rgba(90,61,253,0.1);border-top:1px solid rgba(0,0,0,0.1);border-bottom:1px solid rgba(0,0,0,0.1)}.exo__section:active{background-color:rgba(90,61,253,0.4)}.exo__section--active{background:linear-gradient(0deg, rgba(90,61,253,0.3) 0%, rgba(38,2,235,0.4) 100%);border:1px solid rgba(0,0,0,0.1)}.exo__section--active:hover{background-color:rgba(90,61,253,0.3)}.exo__section--active:active{background-color:rgba(90,61,253,0.4)}.exo__section-name{display:flex;align-items:center;justify-content:space-between;font-size:1em;font-weight:700}.exo__section-name::before{content:"□";margin-right:0.5rem}.exo__section-name strong{font-size:0.8em;flex:1 1;text-align:right}.exo__section-comment{opacity:0.6}.exo__section-time{color:#856ffe;font-size:0.9em;font-weight:600}.exo__section-remove{position:absolute;right:0;top:0;background-color:#ee4368;color:#fff;width:20px;height:20px;height:auto;font-weight:700;border:1px solid rgba(0,0,0,0.1);cursor:pointer;box-sizing:border-box}.exo__section-remove:hover{background-color:#e91543}.exo__index{margin-top:1rem;font-size:0.9em;font-weight:600;border:none;outline:none;background-color:transparent}@media (prefers-color-scheme: dark){.exo__index{color:white}}.exo__title{border:none;outline:none;font-weight:900;font-size:1.1em;margin:1rem 0;text-transform:uppercase;color:#5a3dfd;background-color:transparent}.exo__inputs{display:grid;grid-template-columns:1fr 1fr;width:100%;padding:1.5rem;box-sizing:border-box;grid-gap:0.5rem;gap:0.5rem;background-color:#f5f5f5;border-top:1px solid rgba(0,0,0,0.1)}.exo__inputs input{width:100%;box-sizing:border-box;background-color:white;outline:none;padding:0.5rem 0.5rem;border:1px solid rgba(0,0,0,0.1)}.exo__inputs input:focus{border:1px solid #5a3dfd}.exo__inputs input:first-child{grid-column:1 / span 2}.exo__inputs input:nth-child(4){grid-column:1 / span 2}.exo__inputs input:nth-child(5){grid-column:1 / span 2}.exo__inputs button:last-child{grid-column:1 / span 2}.button{border:none;outline:none;cursor:pointer;color:#fff;font-family:"Work Sans", sans-serif;border:0.5px solid rgba(0,0,0,0.1);padding:0.5rem 1rem;display:flex;align-items:center;justify-content:center;cursor:pointers}.button--default{background-color:#2f2f2f;border:1px solid #fff}.button--default:hover{background-color:#333}.button--default:active{background-color:#000}.button--new{bottom:2rem;background-color:#5a3dfd;font-weight:900;font-size:1.5em;width:50px;height:50px;color:#fff}.button--new:hover{background-color:#4524fd}.button--new:active{background-color:#2f0bfc;transform:scale(0.9)}.button--add{background-color:#5a3dfd;transition:100ms ease-in-out}.button--add:hover{background-color:#4524fd}.button--add:active,.button--add:focus{background-color:#2f0bfc}.button--comment{background-color:grey;transition:100ms ease-in-out;height:30px}.button--comment:hover{background-color:#737373}.button--comment:active{background-color:#676767}.button--section{background-color:#5a3dfd;font-weight:500;transition:50ms ease-in-out;width:100%}.button--section.active{transform:rotate(180deg)}.button--section:hover{background-color:#4524fd}.button--section:active{background-color:#2f0bfc}.button--remove{background-color:#f5f5f5;position:absolute;top:0;right:0;border-radius:0;border-top:none;border-right:none;color:#707070;font-weight:900;transition:100ms ease-in-out}.button--remove:hover{background-color:#ee4368;color:#fff}.button--remove:active{background-color:#e91543;color:#fff}.controls{border-top:1px solid rgba(0,0,0,0.1);background-color:#dbe1f1;width:100%;display:flex;justify-content:space-between;grid-gap:2rem;gap:2rem;padding:1.5rem 2rem;box-sizing:border-box}@media (prefers-color-scheme: dark){.controls{background:transparent;box-shadow:none;border:none;color:white}}

